.section__btn {
  padding: 9px 20px;
  outline: none;
  box-sizing: border-box;
  border-radius: 20px;
  background: none;
  font: 18px/normal "Roboto";
  line-height: 21px;
  text-align: center;

  &:hover {

    //opacity: 0.8;
    //color: #fff;
    //background:  #0064FF;
    //border: 2px solid rgba(0, 100, 255, 0.3);
    cursor: pointer;
  }

  &:disabled {
    opacity: 0.3;
    cursor: default;
    background-color: transparent;
  }
}

.primary {
  color: #0064FF;
  border: 2px solid rgba(0, 100, 255, 0.3);

  :hover {
    background: #0064FF;
    color: #FFFFFF;
  }
}

