.logout {
  border: none;
  width: auto;
  border-radius: 0;
  color: #323232;

  &:hover {
    cursor: pointer;
    background: none;
    color: rgb(50, 50, 50);
  }
}

.subscribe {
  border: 2px solid rgba(0, 180, 100, 0.3);
  color: #00B464;

  &:hover {
    background: #00B464;
    border: 2px solid rgba(0, 180, 100, 0.3);
    color: #FFFFFF;
  }
}


.unsubscribe {
  color: #FF0032;
  border: none;

  &:hover {
    background:  none;
    border: none;
    opacity: 0.5;
    color: #FF0032;
  }
}